@use "../base/mixins.scss" as m;
@use  "../base/variables.scss" as v;



@include m.tablet {

    .contenedor-blog {
        display: grid;
        grid-template-columns: 2fr 1fr;
        column-gap: 2rem;


    }
}

.entrada-blog {
    margin-bottom: 2rem;
    &:last-of-type {
        margin-bottom: 0;
    }

    @include m.tablet {
        display: grid;
        grid-template-columns: 1fr 2fr;
        column-gap: 2rem;
    }
    

    
}

.texto-blog {
    a {
        color: v.$negro;
        display: block;
        margin-top: 3rem;
        @include m.tablet {
            margin-top: 0;
        }
    }

    h4 {
        margin: 0;
        line-height: 1.4;

        &::after {
            content: " ";
            display: block;
            width: 15rem;
            height: .5rem;
            background-color:  v.$verde;
            margin-top: 1rem;
        }

    }
}
span {
    color: v.$amarillo;

}