@import url("https://fonts.googleapis.com/css?family=Lato:300,400,700,900");
@import url("https://fonts.googleapis.com/css?family=Lato:300,400,700,900");
/** Media Queries **/
.no-webp .inicio {
  background-image: url(http://localhost/bienesraices_inicio/build/img/header.jpg);
}

.webp .inicio {
  background-image: url(http://localhost/bienesraices_inicio/build/img/header.webp);
}

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 70rem;
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 0 3rem 0 9rem;
}
.contenido-header h1 {
  margin-top: 8rem;
  text-align: left;
  color: #FFFFFF;
  font-weight: 700;
  line-height: 2;
}

.contenedor-dark-img {
  margin-left: 3rem;
  width: 4rem;
  height: 5rem;
  cursor: pointer;
}

.barra {
  padding-top: 2rem;
  display: flex;
  justify-content: space-between;
  height: 24rem;
  width: 100%;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
  }
}
.barra .menu_right {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 480px) {
  .barra .contenedor-dark-img {
    display: flex;
    align-items: center;
    background-color: #FFFFFF;
    border-radius: 40%;
    padding: 0.5rem;
    margin: 0 auto;
  }
}
@media (min-width: 768px) {
  .barra .contenedor-dark-img {
    background-color: transparent;
    margin-left: 3rem;
  }
}

.navegacion {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.3s linear;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    opacity: 1;
    visibility: visible;
  }
}
.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
}
.navegacion a:hover {
  color: #71B100;
}
.navegacion a.cerrar-sesion {
  color: rgb(202, 8, 8);
}
.navegacion a.cerrar-sesion:hover {
  color: #9e0606;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}

.mostrar_menu {
  visibility: visible;
  opacity: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .mostrar_menu {
    display: flex;
    flex-direction: row;
    align-items: center;
  }
}

@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 4rem;
  cursor: pointer;
}

.icono {
  text-align: center;
}
.icono img {
  height: 10rem;
}
.icono h3 {
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.boton-amarillo {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.boton-amarillo:hover {
  background-color: #b96f07;
  cursor: pointer;
}

.boton-amarillo-block {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.boton-amarillo-block:hover {
  background-color: #b96f07;
  cursor: pointer;
}

.btn-verde {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 2rem;
}

.boton-verde {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.boton-verde:hover {
  background-color: #578800;
  cursor: pointer;
}

.boton-rojo-block {
  background-color: rgb(202, 8, 8);
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.boton-rojo-block:hover {
  background-color: #a30606;
  cursor: pointer;
}
.boton-rojo-block {
  width: 100%;
}

.boton-azul-block {
  background-color: rgb(62, 140, 219);
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.boton-azul-block:hover {
  background-color: #2678ca;
  cursor: pointer;
}

.no-webp .img-contacto {
  background-image: url(http://localhost/bienesraices_inicio/build/img/encuentra.jpg);
}

.webp .img-contacto {
  background-image: url(http://localhost/bienesraices_inicio/build/img/encuentra.webp);
}

.img-contacto {
  background-position: center center;
  background-size: cover;
  padding: 5rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.img-contacto h2 {
  font-size: 4rem;
  font-weight: 900;
  color: #FFFFFF;
}
.img-contacto p {
  font-size: 1.8rem;
  color: #FFFFFF;
}

@media (min-width: 768px) {
  .contenedor-blog {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
.entrada-blog {
  margin-bottom: 2rem;
}
.entrada-blog:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}

.texto-blog a {
  color: #000000;
  display: block;
  margin-top: 3rem;
}
@media (min-width: 768px) {
  .texto-blog a {
    margin-top: 0;
  }
}
.texto-blog h4 {
  margin: 0;
  line-height: 1.4;
}
.texto-blog h4::after {
  content: " ";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #71B100;
  margin-top: 1rem;
}

span {
  color: #E08709;
}

.testimonial {
  background-color: #71B100;
  padding: 5rem;
  border-radius: 5rem;
  font-size: 2.2rem;
  color: #FFFFFF;
}
.testimonial blockquote {
  padding-left: 5rem;
  position: relative;
}
.testimonial blockquote::before {
  content: " ";
  position: absolute;
  left: -2rem;
  top: -1.6rem;
  background-image: url(http://localhost/bienesraices_inicio/build/img/comilla.svg);
  display: inline-block;
  width: 7rem;
  height: 8rem;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}
.testimonial p {
  text-align: right;
  color: #FFFFFF;
}

.footer {
  padding: 2.5rem 0;
  background-color: #333333;
}
.footer .contenedor--footer {
  padding: 2.5rem;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
}
.footer:last-of-type {
  margin-bottom: 0;
}
.footer .navegacion {
  visibility: visible;
  opacity: 1;
}